.engagement {
  gap: 5.625rem;
}
@media (min-width: 40em) {
  .engagement {
    flex-direction: row;
    justify-content: space-around;
  }
}
.engagement > * {
  min-width: 11.25rem;
}
.engagement__icon {
  display: block;
  margin-bottom: 0.625rem;
  margin-left: 0.3125rem;
}
.engagement__data {
  display: block;
  margin-bottom: 0.3125rem;
  font-family: var(--font-heading);
  font-size: 3.75rem;
  font-weight: 700;
}
@media (min-width: 40em) {
  .engagement__data {
    font-size: 5rem;
  }
}
@media (min-width: 71.875em) {
  .engagement__data {
    font-size: 6.25rem;
  }
}